<template>
  <div>
    <top-header title-txt="添加商品"></top-header>
    <div style="height: 1.61rem;"></div>
    <!--添加商品-->
    <div class="add_header">
      <!--title-->
      <div class="header_title">
        <div class="title_left">
          <span class="must">*</span>
          <span>为必填项</span>
        </div>
      </div>
      <!--item-->
      <div class="add_box">
        <div class="add_content_left">
          <span class="must">*</span>
          <span class="tips">商品名称</span>
        </div>
        <input type="text" placeholder="输入商品名称" v-model.trim="productName">
      </div>
      <!--商品分类-->
      <router-link to="/category">
        <div class="add_box">
          <div class="add_content_left">
            <span class="must">*</span>
            <span class="tips">商品分类</span>
          </div>
          <span>{{categoryTxt}}<img src="../assets/images/icon_arrow_right.png"/></span>
        </div>
      </router-link>
      <!--商品品牌-->
      <router-link to="/brand">
        <div class="add_box">
        <div class="add_content_left">
          <span class="must">*</span>
          <span class="tips">商品品牌</span>
        </div>
        <span>{{brandInfo}} <router-link to="/brand"><img src="../assets/images/icon_arrow_right.png"/></router-link></span>
      </div>
      </router-link>
      <!--商品介绍-->
      <div class="add_box" style="border: none;">
        <div class="add_content_left">
          <span class="must">*</span>
          <span class="tips">商品介绍</span>
        </div>
      </div>
      <div class="add_content_bottom">
        <textarea placeholder="输入该商品的详细描述" v-model.trim="introduction"></textarea>
      </div>
      <!--商品头图-->
      <div class="product_logo_box">
        <div class="add_img_box">
          <div class="img_content_left">
            <span class="must">*</span>
            <span class="tips">商品头图</span>
          </div>
          <div class="img_up">
            <van-uploader :after-read="onProductLogoRead" accept="image/*" multiple>
              <van-icon name="photograph" />
            </van-uploader>
          </div>
          <span class="img_tips">图片上传要求：单张图片小于10M此处最多只能添加三张图片。</span>
        </div>
        <div class="img_select_box">
          <div class="img_box" v-for="(path,index) in productLogo">
            <img class="img_delete" src="../assets/images/icon_img_delete.png" @click="deleteLogoImage(index)"/>
            <img class="img_select" :src="path"/>
          </div>
        </div>
      </div>
      <!--商品图片-->
      <div class="product_imgs_box">
        <div class="add_img_box">
          <div class="img_content_left">
            <span class="must">*</span>
            <span class="tips">商品图片</span>
          </div>
          <div class="img_up">
            <van-uploader :after-read="onProductOtherRead" accept="image/*" multiple>
              <van-icon name="photograph" />
            </van-uploader>
          </div>
          <span class="img_tips">图片上传要求：单张图片小于10M此处最多只能添加三张图片。</span>
        </div>
        <div class="img_select_box">
          <div class="img_box" v-for="(path,index) in productImages">
            <img class="img_delete" src="../assets/images/icon_img_delete.png" @click="deleteOtherImage(index)"/>
            <img class="img_select" :src="path"/>
          </div>
        </div>
      </div>
      <div class="bar"></div>
      <!--------商品属性-------------->
      <div class="add_box">
        <div class="add_content_left">
          <span class="tips">商品属性</span>
        </div>
      </div>
      <div class="attributes_box" v-for="(item,index) in attributesContent">
        <div class="img_content_left">
          <span class="must">*</span>
          <span class="tips">{{item.label}}</span>
        </div>
        <input class="attributes_input" type="text" placeholder="请输入内容"  v-model.trim="attributesContent[index].value"/>
      </div>
      <div class="bar"></div>
      <!--商品规格-->
      <router-link to="/specEdit">
        <div class="add_box">
          <div class="add_content_left">
            <span class="must">*</span>
            <span class="tips">商品规格</span>
          </div>
          <span>请添加<img src="../assets/images/icon_arrow_right.png"/></span>
        </div>
      </router-link>
      <div class="spec_list" v-for="(specData,index) in specDataList" @click="checkDetailSpec(index)">
        <span class="spec_details_name">规格 {{index + 1}}</span>
        <div class="check_spec_box">
          <span class="check">查看</span>
          <img class="arrow" src="../assets/images/icon_my_arrow.png" />
        </div>
      </div>
      <div class="bar"></div>
      <!--满减活动-->
      <div class="add_box">
        <div class="add_content_left">
          <span class="must">*</span>
          <span class="tips">满减活动</span>
        </div>
        <span>
          <radio-button radio_name="active" radio_title="参与" style="margin-right: 1rem;" is-checked="true" radio_val="1" @radioChange="getisJionActivity"></radio-button>
          <radio-button radio_name="active" radio_title="不参与" radio_val="2" @radioChange="getisJionActivity"></radio-button>
        </span>
      </div>
      <!--限购数量-->
      <div class="add_box">
        <div class="add_content_left">
          <span class="tips">单笔订单限购数量</span>
        </div>
        <input type="number" placeholder="输入限购数量" v-model.trim="limitNum">
      </div>
      <!--上架状态-->
      <div class="add_box" style="border: none;">
        <div class="add_content_left">
          <span class="must">*</span>
          <span class="tips">上架状态</span>
        </div>
        <span>
          <radio-button radio_name="g_status" radio_title="审核通过后，立即自动上架" is-checked="true" @radioChange="getProductState"></radio-button>
        </span>
      </div>
      <div class="add_box">
        <div class="add_content_left" style="visibility: hidden;">
          <span class="must">*</span>
          <span class="tips">上架状态</span>
        </div>
        <span>
          <radio-button radio_name="g_status" radio_title="审核通过后，手动操作上架" @radioChange="getProductState"></radio-button>
        </span>
      </div>
    </div>
    <!--协议部分-->
    <div class="protocol">
      <p class="agreement">
        <van-checkbox v-model.trim="protocolChecked">
          <img id="img_chk" slot="icon" slot-scope="props" :src="props.checked ? icon.active : icon.normal"/>
          我已经阅读并同意
          <a href="" style="color: #4cc3ad;">《开店服务协议》</a>
        </van-checkbox>
      </p>
    </div>
    <span class="preserve_btn" @click="uploadImg">确定添加</span>
  </div>
</template>

<script>
  import TopHeader from '../components/TopHeader'
  import RadioButton from '../components/common/RadioButton.vue'
  import Toast from '../widget/Toast';
  import eventBus from  '../utils/eventBus'
  import * as API from '../service/API';
  import Loading from '../widget/loading/loading'

  let person_index = 0;
  let other_indext = 0;

  export default {
    data(){
      return {
        protocolChecked:true,
        productName:'',
        introduction:'',
        brandInfo:'',
        brandInfoIds:'', //用于上传的 品牌ids
        limitNum:'',
        specDataList:[],
        productState:'4',     //4 审核通过 立即上架   1 审核通过 手动上架
        isJionActivity:'1', // 是否参加活动，默认是参加
        categoryInfo:{}, // 捕获返回的对象
        categoryTxt:'请选择',  // 为了方便显示，拼接的分类名
        productLogo:[], // 商品头图
        productLogoExt:[], // 商品头图后缀
        productImages:[], //商品图片
        productImagesExt:[], //商品图片后缀
        responseLogoUrl:[],  //上传成功返回商品logo
        responseImages:[],   //上传返回的其他商品地址
        attributesInfo:[],   // 属性列表
        attributesContent:[],   // 保存输入的内容， 拷贝属性列表内容
        icon: {
          normal: '../../static/images/nocheck_out.png',
          active: '../../static/images/check_out.png'
        }
      }
    },
    created(){
      eventBus.$on('categorySelected',this.getCategory);
      eventBus.$on('getBrand',this.getBrandSelected);
      eventBus.$on('specData',this.getSpecData);
    },
    methods:{
      getisJionActivity(val){
        this.isJionActivity = val;
      },
      getProductState(val){
        this.productState = val;
      },
      checkDetailSpec(index){
        let spec = this.specDataList[index];
        eventBus.$emit('spec',spec);
        this.$router.push(`/spec/${index + 1}`);
      },
      onProductLogoRead(file){
        let base64 = file.content;  // 文件路径
        this.productLogo.push(base64);
        let fileName = file.file.name; //文件名
        let ext = fileName.substring(fileName.lastIndexOf(".")+1).toLowerCase();  //后缀名
        this.productLogoExt.push(ext);
      },
      onProductOtherRead(file){
        let base64 = file.content;  // 文件路径
        this.productImages.push(base64);
        let fileName = file.file.name; //文件名
        let ext = fileName.substring(fileName.lastIndexOf(".")+1).toLowerCase();  //后缀名
        this.productImagesExt.push(ext);
      },
      deleteLogoImage(position){
        this.productLogo.splice(position,1);
        this.productLogoExt.slice(position,1);
      },
      // 删除指定图片，position，图片位置
      deleteOtherImage(position){
        this.productImages.splice(position,1);
        this.productImagesExt.slice(position,1);
      },
      // 获取传递过来的规格对象
      getSpecData(content){
        if(content){
          var has_content = false;
          for (var index in this.specDataList ){
            if(this.specDataList[index].gn_id == content.gn_id){
              this.specDataList[index] = content;
              has_content = true;
            }
          }

          if(!has_content){
            this.specDataList.push(content);
          }
        }
      },
      getCategory(content){
        //要判断有值，再添加，避免空值覆盖
        if(content){
          this.categoryInfo = content;
          this.categoryTxt = this.categoryInfo.firLevelName + " " + this.categoryInfo.secLevelName
            + " " + this.categoryInfo.thiLevelName;
          this.getAttributeData( this.categoryInfo.firLevelId);
        }
      },
      //id 一级分类id
      getAttributeData(id){
        let loading = new Loading();
        loading.show();
        this.$get(API.ATTRIBUTE_DATA + `?gc_id=${id}`).then((response)=>{
          if(response.code != 200){
            new Toast(response.msg).show();
            return;
          }
          this.attributesInfo = response.data;
          this.attributesContent = this.attributesInfo.slice();
          // 删除首个元素，首个元素中是数据个数，不要显示出来
          this.attributesContent.splice(0,1);
          loading.close();
        }).then((error)=>{
          loading.close();
        });
      },
      getBrandSelected(content){
        if(content){
          let selectBrandIds = content.brandIds;
          let selectBrandNames = content.brandNames;
          // for(let i = 0; i<selectBrandIds.length;i++){
          //   this.brandInfo = + selectBrandNames[i] + ","  + this.brandInfo;
          //   this.brandInfoIds = selectBrandIds[i] + "," + this.brandInfoIds;
          // }
          this.brandInfo = selectBrandNames[0];
          this.brandInfoIds = selectBrandIds[0];
        }
      },
      saveProduct(){
        let loading = new Loading();
        loading.show();
        let data = {};
        data.gc_first_id = this.categoryInfo.firLevelId;
        data.gc_second_id = this.categoryInfo.secLevelId;
        data.gc_three_id = this.categoryInfo.thiLevelId;
        data.g_name = this.productName;
        data.gb_id = this.brandInfoIds;
        data.g_desc = this.introduction;
        data.is_join_activity = this.isJionActivity;
        data.g_order_num = this.limitNum;
        data.goodsHeadImg = this.responseLogoUrl[0];
        data.goodsImg = this.responseImages;
        data.total_attr = this.attributesInfo[0].value;
        data.g_status = this.productState;  //4 审核通过 立即上架   1 审核通过 手动上架
        let g_standard = '';  //商品规格，逗号分隔
        for(let i = 0; i< this.specDataList.length; i++){
          g_standard = this.specDataList[i].gn_id + ',';
        }
        data.g_standard = g_standard;
        for(let i = 0; i<this.attributesContent.length; i++){
          let attributesName = this.attributesContent[i].name;
          data[attributesName] = this.attributesContent[i].value;
        }
        let requestUrl = API.BASEURL + '/goods/edit_goods/0.html';
        this.$post(requestUrl,data).then((response)=>{
          if(response.code != 200){
            new Toast(response.msg).show();
            return;
          }
          this.$router.replace("/addGoodSuccess");
          loading.close();
        }).then((error)=>{
          loading.close();
        });
      },
      // 保存验证
      verification(){
        if(false == this.protocolChecked){
          new Toast('请先同意服务协议')
          return false;
        }
        if(!this.productName){
          new Toast("请输入商品名称").show();
          return false;
        }
        if(!this.introduction){
          new Toast("请输入商品描述").show();
          return false;
        }
        if(!this.productLogo){
          new Toast("请添加商品头图").show();
          return false;
        }
        if(this.productImages.length==0){
          new Toast("请添加商品图片").show();
          return false;
        }
      },
      //图片上传
      uploadImg(){
        if(false == this.verification()){
          return;
        }
        this.postLogoImage(this.productLogoExt[0]);
      },
      //上传个人认证图片
      postLogoImage(ext){
        let loading = new Loading();
        loading.show();
        this.$postImg(process.env.UPLOAD_IMG_URL, { "ext": ext, "content": this.productLogo[person_index]}).then((response) => {
          this.responseLogoUrl.push(response.result.url);
          if(person_index < this.productLogo.length){
            person_index = person_index + 1;
            this.postLogoImage(this.productLogoExt[person_index]);
          }else {
            if(this.productImages&&this.productImages.length>0){
              this.postOtherImage(this.productImagesExt[0]);
            }else{
              this.saveProduct();
            }
          }
          loading.close();
        }).then((error)=>{
          loading.close();
        });
      },
      //上传其他资料图片
      postOtherImage(ext){
        let loading = new Loading();
        loading.show();
        this.$postImg(process.env.UPLOAD_IMG_URL, { "ext": ext, "content": this.productImages[other_indext]}).then((response) => {
          this.responseImages.push(response.result.url);
          if(other_indext < this.productImages.length){
            other_indext = other_indext + 1;
            this.postOtherImage(this.productImagesExt[other_indext]);
          }else{
            this.saveProduct();
          }
          loading.close();
        }).then((error)=>{
          loading.close();
        });
      },
    },
    components:{TopHeader,RadioButton}
  }
</script>

<style lang="scss" scoped>
  @import "../style/common.scss";
  /*  灰色头部 */
.add_header {
  .header_title {
    background-color: #eeeeee;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 0.46rem;
    padding-right: 0.46rem;
    height: 0.98rem;
    .title_left {
      display: flex;
      justify-content: center;
      .must{
        color: #e36348;
      }
      img {
        padding-top: 0.08rem;
        width: 0.1rem;
        height: 0.4rem;
      }
      span {
        padding-left: 0.1rem;
        color: #666666;
        font-size: 0.4rem;
      }
    }
  }
  /*  包input的盒子 */
  .add_box {
    display: flex;
    justify-content: space-between;
    height: 1.74rem;
    align-items: center;
    border-bottom: 1px solid #eeeeee;
    .add_content_left {
      display: flex;
      justify-content: center;
      padding-left: 0.51rem;
      .must {
        color: #e36348;
        padding-top: 0.1rem;
        padding-right: 0.1rem;
      }
      .tips {
        font-size: 0.48rem;
        color: $font_100;
      }
    }
    img{
      width: 0.2rem;
      height: 0.3rem;
    }
    span{
      font-size: .48rem;
      color: #666666;
      padding-right: 0.51rem;
      img{
        margin-left: .15rem;
      }
    }
    input{
      text-align: right;
      padding-right: 0.51rem;
    }
    input::-webkit-input-placeholder{
      text-align: right;
    }
  }
  /*属性列表*/
  .add_content_bottom{
    display: flex;
    padding: 0 .51rem;
    border-bottom: 1px solid #eeeeee;
    textarea{
      width: 100%;
      display: block;
      height: 2.05rem;
      border: 1px dashed #989898;
      padding: .3rem;
      margin-bottom: .6rem;
    }
  }
  .attributes_box{
    display: flex;
    height: 1.80rem;
    justify-content: space-between;
    align-items: center;
    .img_content_left {
      display: flex;
      justify-content: center;
      padding-left: 0.51rem;
      .must {
        color: #e36348;
        padding-top: 0.1rem;
        padding-right: 0.1rem;
      }
      .tips {
        white-space: nowrap;
        font-size: 0.48rem;
        color: $font_100;
      }
    }
    .attributes_input{
      text-align: right;
      padding-right: 0.62rem;
      font-size: 0.48rem;
    }
  }
   /*规格列表*/
  .spec_list{
    height: 1.74rem;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .spec_details_name{
      padding-left: 1.18rem;
      color: #1a1b39;
      font-size: 0.48rem;
    }
    .check_spec_box{
      display: flex;
      align-items: center;
      padding-right: 0.52rem;
      .check{
        padding-right: 0.3rem;
        color: #cdcdd1;
        font-size: 0.48rem;
      }
      .arrow{
        width: 0.2rem;
        height: 0.3rem;
      }
    }
  }

   /*   bar隔离条   */
  .bar{
    height: .26rem;
    background-color: #eee;
    width: 100%;
  }
  /*  包含图片上传的盒子 */
  .add_img_box {
    padding-top: 0.51rem;
    padding-bottom: 0.46rem;
    display: flex;
    border-bottom: 1px solid #eeeeee;
    .img_content_left {
      display: flex;
      justify-content: center;
      padding-left: 0.51rem;
      .must {
        color: #e36348;
        padding-top: 0.1rem;
        padding-right: 0.1rem;
      }
      .tips {
        white-space: nowrap;
        font-size: 0.48rem;
        color: $font_100;
      }
    }
    .img_up {
      padding-left: 0.45rem;
      width: 1.88rem;
      height: 1.88rem;
    }
    .img_tips {
      padding-left: 0.45rem;
      color: #666666;
      font-size: 0.32rem;
    }
  }
  /*图片展示部分*/
  .img_select_box{
    background-color: white;
    padding: 0.55rem 0.51rem;
    .img_box{
      display: inline-block;
      position: relative;
      width: 1.88rem;
      height: 1.88rem;
      padding-right: 0.2rem;
      .img_delete{
        position: absolute;
        right: 0rem;
        top: -0.2rem;
        width: 0.52rem;
        height: 0.52rem;
      }
      .img_select{
        padding-right: 0.2rem;
        width: 1.88rem;
        height: 1.88rem;
      }
    }
  }
}
/*协议*/
.protocol{
  padding-top: 0.3rem;
  padding-bottom: 2.56rem;
  background-color: #eeeeee;
.agreement{
  padding-left: 0.51rem;
  font-size:.4rem;
  color: $font_100;
i{
  display: inline-block;
  width: .6rem;
  height: .6rem;
  margin-right: .2rem;
}
img{
  padding-top: 0.1rem;
  width: 0.83rem;
  height: 0.82rem;
}
}
